import React from "react";
import { ChainId, getChainName } from 'sdk'
import styled from 'styled-components'
import Button from "../../../components/Button/Button";

const ChainRoot = styled.div`
  margin-right: 10px;
  `

interface Props {
  chainId: ChainId,
  logout: () => void;
}

const ChainBlock: React.FC<Props> = ({ logout, chainId }) => {
  return (
    <ChainRoot>
        <Button
          scale="sm"
          variant="tertiary"
        >
          {getChainName(chainId)}
        </Button>
    </ChainRoot>
  );
};

export default React.memo(ChainBlock, (prevProps, nextProps) => prevProps.chainId === nextProps.chainId);
